.quick-edit-container {
    position: fixed;
    left: 0;
    top: 0;
    height: 100vh;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.578);
    z-index: 1000;

    .quick-edit {
        display: flex;
        position: absolute;
        gap: 8px;
    }

    .quick-edit-body {

        .task-preview.styled.img {
            padding: 0;
            display: flex;
            flex-direction: column;

            img {
                object-fit: cover;
                height: 100%;
                width: 100%;
                border-top-right-radius: 3px;
                border-top-left-radius: 3px;
            }

            .task-info {
                flex: 1;
                width: 100%;
                align-items: stretch;
                background-color: #fff;
                border-bottom-right-radius: 3px;
                border-bottom-left-radius: 3px;
            }

            textarea {
                margin: 4px 0;
            padding: 6px 8px;
            }
        }

        
    }

    textarea {
        color: #0d1e42;
        min-height: 90px;
        font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
        line-height: 20px;
        border-radius: 3px;
        margin: 0;
        max-height: 256px;
        padding: 0;
        resize: none;
        overflow: hidden;
        overflow-wrap: break-word;
        border: none;

        &:focus {
            outline: none;
        }
    }

    .save-btn {
        @include nice-btn
    }

    .quick-edit-actions {
        display: flex;
        flex-direction: column;
        gap: 4px;

        .quick-edit-btn {
            background: #0009;
            border: none;
            border-radius: 3px;
            color: #e6e6e6;
            font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Droid Sans, Helvetica Neue;
            font-size: 14px;
            padding: 6px 12px 6px 8px;
            transition: transform 0.075s;

            &:hover {
                background: #000c;
                color: #fff;
                transform: translateX(5px);
            }
        }
    }
}