.create-board-preview {
   display: flex;
   justify-content: center;
   padding-bottom: 8px;

   .preview-img {
      width: 200px;
      height: 120px;
      background: none;
      background-position: center center;
      background-size: cover;
      border-radius: 3px;
      box-shadow: 0 7px 15px rgb(0 0 0 / 15%);
      display: flex;
      align-items: center;
      justify-content: center;
      background-position: 50% !important;
      background-size: cover !important;

      img {
         border: 0;
      }
   }
}

.create-board-background {

   .label {
      @include task-sub-header();
      margin-top: 16px;
      margin-bottom: 4px;
   }

   .img-container {
      display: grid;
      grid-template-columns: 1fr 1fr;
      gap: 8px;
      padding: 4px;
      margin: 0 -4px;
      overflow-x: hidden;
      grid-template-columns: 1fr 1fr 1fr 1fr 1fr;

      button {
         margin: 0;
         border-radius: 3px;
         box-shadow: none;
         cursor: pointer;
         border: none;
         margin: 0;
         border-radius: 3px;
         background-color: transparent;
         background-size: cover;
         background-repeat: no-repeat;
         background-position: center;
         width: 64px;
         height: 40px;

         &:hover {
            filter: saturate(85%) brightness(85%);
         }
   
         &:focus {
            filter: saturate(60%) brightness(60%);

            &::after {
               content: "✓";
               color: white;

            }
         }
      }

   }

   .color-container {
      display: grid;
      gap: 8px;
      padding: 4px;
      margin: 0 -4px;
      overflow-x: hidden;
      grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr;

      button {
         margin: 0;
         border-radius: 3px;
         background-size: cover;
         background-repeat: no-repeat;
         background-position: center;
         box-shadow: none;
         width: 40px;
         height: 32px;
         cursor: pointer;
         border: none;

         &:hover {
            filter: saturate(85%) brightness(85%);
         }
   
         &:focus {
            filter: saturate(60%) brightness(60%);

            &::after {
               content: "✓";
               color: white;

            }
         }
      }
   }
}

.create-board-title {
   .label {
      @include task-sub-header();
      margin-top: 12px;
      margin-bottom: 0px;

      span {
         color: #eb5a46;
      }
   }

   .board-title {
      input {
         margin: 0 0 12px;
         width: 100%;
         background-color: #fafbfc;
         border: none;
         border-radius: 3px;
         box-shadow: inset 0 0 0 2px #eb5a46;
         box-sizing: border-box;
         display: block;
         line-height: 20px;
         margin-bottom: 6px;
         outline: none;
         padding: 8px 12px;
         transition-duration: 85ms;
         transition-property: background-color, border-color, box-shadow;
         transition-timing-function: ease;
         color: #172b4d;
         font-size: 14px;
         font-weight: 400;

         &:focus {
            background-color: #fff;
            box-shadow: inset 0 0 0 2px #0079bf;
         }

         &:hover {
            box-shadow: inset 0 0 0 2px #eb5a46;
            background-color: #ebecf0;
         }
      }

   }

   .title-msg {
      display: flex;
      flex-direction: row;
      margin-top: 4px;

      span {
         margin-right: 8px;
      }

      p {
         margin: 0 0 8px;
         color: #172b4d;
         font-size: 14px;
         line-height: 20px;
         font-weight: 400;
      }

   }

   .cover-btn {
      @include button-link();
      text-align: center;
      background-color: rgba(9, 30, 66, 0.04);
      box-shadow: none;
      border: none;
      color: #a5adba;
      cursor: not-allowed;
      justify-content: center;


      &.allowed {
         background-color: #0079bf;
         box-shadow: none;
         border: none;
         color: #ffffff;
         cursor: pointer;

         &:hover {
            background-color: #026aa7;
            box-shadow: none;
            border: none;
            color: #ffffff;
      }

   }

}
}