.task-details-attachments {
   clear: both;
   margin-bottom: 24px;
   position: relative;

   .attachments-title-container {
      @include task-category()
   }

   .attachments-body-container {
      margin-left: 40px;

      .attachments-section-container {
         border-radius: 3px;
         margin: 0 0 8px;
         min-height: 80px;
         overflow: hidden;
         position: relative;

         .thumbnail-img {
            background-color: rgba(9, 30, 66, 0.0392156863);
            background-position: 50%;
            background-repeat: no-repeat;
            background-size: contain;
            border-radius: 3px;
            height: 80px;
            left: 0;
            margin-top: -40px;
            position: absolute;
            text-align: center;
            text-decoration: none;
            top: 50%;
            width: 112px;
            z-index: 1;
         }

         .thumbnail-info {
            box-sizing: border-box;
            cursor: pointer;
            margin: 0;
            min-height: 80px;
            padding: 8px 8px 8px 128px;
            z-index: 0;

            &:hover {
               background-color: rgba(9, 30, 66, 0.0392156863);
            }

            .thumbnail-name {
               color: #172b4d;
               font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Droid Sans, Helvetica Neue, sans-serif;
               font-size: 14px;
               line-height: 20px;
               cursor: pointer;
               word-wrap: break-word;
               font-weight: 700;
               margin-bottom: 8px;
            }

            .thumbnail-details {
               color: #5e6c84;
               margin-bottom: 8px;

               .thumbnail-date {
                  color: #5e6c84;
                  margin-bottom: 8px;
                  font-size: 14px;
                  font-weight: 400;
                  line-height: 20p
               }

            }

            .thumbnail-cover {
               color: #5e6c84;
      
               .icon-make-cover {
                  font-size: 14px;
                  padding: 7px;

                  svg {
                     height: 15px;
                     width: 15px;
                  }
               }
            }
         }

      }

      .attachments-add {
         @include button-link();
         max-width: 150px;
         display: inline-flex;
         text-align: center;
         
      }
   }

}

.button-att {
   @include button-underline();
}
