.member-section {
   overflow-x: hidden;
   overflow-y: auto;

   .search-box {
      input {
         margin: 4px 0 12px;
         width: 100%;
         background-color: #fafbfc;
         border: none;
         border-radius: 3px;
         box-shadow: inset 0 0 0 2px #dfe1e6;
         box-sizing: border-box;
         display: block;
         line-height: 20px;
         margin-bottom: 12px;
         outline: none;
         padding: 8px 12px;
         transition-duration: 85ms;
         transition-property: background-color, border-color, box-shadow;
         transition-timing-function: ease;
         color: #172b4d;
         font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Droid Sans, Helvetica Neue, sans-serif;
         font-size: 14px;
         font-weight: 400;

         &:focus {
            background-color: #fff;
            box-shadow: inset 0 0 0 2px #0079bf;
         }
      }
   }

   .members-box {
      margin-top: 12px;

      .label {
         @include task-sub-header();
         margin-bottom: 6px;
      }

      ul {
         list-style: none;
         margin: 0;
         padding: 0;

         li {
            margin-bottom: 6px;

            &:hover {
               background-color: #091e420a;
               cursor: pointer;
               border-radius: 3px;

            }

            .member-list {
               padding-right: 28px;
               border-radius: 3px;
               display: block;
               height: 37px;
               line-height: 32px;
               margin-bottom: 2px;
               overflow: hidden;
               padding: 4px;
               position: relative;
               text-decoration: none;
               text-overflow: ellipsis;
               white-space: nowrap;
               color: #172b4d;

               .member {
                  @include task-member-button(tomato);
                  margin-right: 8px;

               }

               .member-img {
                  cursor: pointer;
                  float: left;
                  border-radius: 50%;
                  width: 32px;
                  height: 32px;
                  background-repeat: round;
                  background: center;
                  background-size: cover;
                  margin-right: 3px;

                  object-fit: cover;
                  max-width: 100%;
                  margin-right: 8px;
                  margin: 0 8px 4px 0;
               }

               .member-txt {
                  color: #172b4d;
                  font-size: 14px;
                  font-weight: 400;
               }

               .member-icon {
                  background-clip: content-box;
                  background-origin: content-box;
                  padding: 4px;
                  position: absolute;
                  right: 0;
                  top: 0;

                  svg {
                     height: 14px;
                     width: 14px;
                  }
               }
            }
         }


      }



   }

}